/* Додано стилі для Modal та Overlay */
.Overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: rgba(0, 0, 0, 0.8);
  z-index: 1200;
}

.Modal {
  max-width: 80%;
  max-height: 80%;
}

.Modal img {
  width: 100%;
  height: 100%;
  object-fit: contain;
}